import React, { useCallback,useRef } from "react";
import styled from "styled-components";
import RegexHelper from '../utils/RegexHelper';
import { useNavigate } from 'react-router-dom';

import crypto from 'crypto-js';
import axios from "axios";
// import Arrow from "../assets/icon/arrow.png";

const SignupContainer = styled.div`
    width: 100%;
    padding-top: 20%;
    padding-bottom: 20%;

    .signup_content {
        width: 80%;
        display: flex;
        margin: 0 auto;
        flex-direction: column;
        flex-wrap: wrap;
        align-content: center;

        h3 {
            padding-bottom: 12%;
            display: flex;
            justify-content: center;
        }

        form {
            width: 100%;

            .message {
                display: block;
                padding-bottom: 2%;
                font-size: 14px;

                span{
                    font-size: 14px;
                    color: red;
                }
            }

            .err_msg {
                color: red;
                display: flex;
                font-size: 14px;
                padding: 4% 0 4% 0;
            }

            .input_text {
                width: 90%;
                height: 16px;
                padding: 5%;
            }

            .birth_area {
                width: 100%;
                display: flex;
                justify-content: space-between;

                .user_birth {
                    width: 22%;
                    height: 16px;
                    padding: 5%;
                }

                .user_mm {
                    width: 30%;
                    height: auto;
                    padding: 4%;
                }
            }

            .email_area {
                width: 100%;
                
                .input_box{
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                }

                .user_email {
                    width: 38%;
                    height: 16px;
                    padding: 5%;
                }

                .user_email_sel {
                    width: 49%;
                    height: auto;
                    padding: 5%;
                }
            }
        }
    }
`;

const Signup = () => {
    const navigate = useNavigate();
    const signup_form = useRef();

    /**      */
    const month = [];
    for (let i = 1; i < 13; i++) month.push(i);

    //   
    let userid = null;
    let password = null;
    let passwordCheck = null;
    let username = null;
    let birthday = null;
    let email = null;


    /**    
     * @param : userid
     * @returns :  ->  error.response.data.rtmsg:"  ."
     *             -> json.data.rtmsg:"  "
    */
    const isMember = async(userid)=>{
        let json = null;
        try {
            json = await axios.post("/members/ismember",{userid:userid})
        } catch (error) {
            return error.response.data.rtmsg;
        }
        const msg = json.data.rtmsg;
        return msg;
    };

    /**input onBlur  */
    const onBlur = useCallback(e => {
        const current = e.target;
        const sibling = current.nextElementSibling;
        try {            
            switch (current.id) {
                case 'userid':
                    RegexHelper.value(current.value,' ');
                    RegexHelper.useridCheck(current.value,' + 8~16 .');
                    const msg = isMember(current.value);
                    msg.then((PromiseResult)=>{
                        current.nextElementSibling.innerHTML = PromiseResult;
                        current.nextElementSibling.style.color = "#0058FF";
                    })
                    break;
                case 'password':
                    RegexHelper.value(current.value,' ');
                    current.nextElementSibling.innerHTML = " .";
                break;
                case 'passwordCheck':
                    RegexHelper.value(current.value,'  ');
                    RegexHelper.compareTo(signup_form.current.password.value,current.value,'  .');
                    current.nextElementSibling.innerHTML = " .";
                break;
                case 'username':
                    RegexHelper.value(current.value,' ');
                    current.nextElementSibling.innerHTML = "ok";
                break;
                case 'birth_year':
                    if(current.value){
                        RegexHelper.value(current.value,' ');
                    }
                break;
                case 'birth_month':
                    if(current.value){
                        RegexHelper.value(current.value,'  ');
                    }
                break;
                case 'birth_day':
                    if(current.value){
                        RegexHelper.value(current.value,'  ');
                    }
                break;
                case 'input_email':
                    RegexHelper.value(current.value, ' .');
                    RegexHelper.engNum(current.value, ' , .');
                break;
                case 'input_domain':
                    RegexHelper.value(current.value, ' .');
                break;
                default:
                break;
            }
        } catch (error) {
            // console.log(error);
            
            sibling.style.color = "red";
            sibling.innerHTML = '';
            sibling.innerHTML = error.message;        
            throw error;
        }
    },[]);

    /** post  axios-hooks   */
    // const [{ data, loading, error }, refetch] = useAxios({
    //       url: 'http://itpaper.co.kr:9910/members',
    //       method: 'POST'
    //     },
    //     { manual: true })

    /**submit :   ,  post */
    const onSubmit = async(e) => {
        e.preventDefault();

        const current = e.target;
        userid = current.userid.value;
        password = current.password.value;
        passwordCheck = current.passwordCheck.value;
        username = current.username.value;
        birthday = current.birth_year.value+'-'+current.birth_month.value+'-'+current.birth_day.value;
        email = current.input_email.value+current.input_domain.value;
        
        /***/
        try {
            RegexHelper.value(userid,' ');
            RegexHelper.engNum(userid,' , .');
            RegexHelper.value(password,' ');          
            RegexHelper.value(passwordCheck,'  ');
            RegexHelper.value(username,' ');
            if (birthday) {
                RegexHelper.value(birthday,' ');
            };
            RegexHelper.value(email, ' .');
            RegexHelper.email(email,'  .');

        } catch (error) {
            // console.log(error);
            window.alert(error.message);
            throw error;
        }

        /** _crypto-js  */
        // AES  
        const secretKey = process.env.REACT_APP_CRYPTO_KEY;
        password = crypto.AES.encrypt(password, secretKey).toString();
        passwordCheck = crypto.AES.encrypt(passwordCheck, secretKey).toString();
        
        /**      */
        const input_data={
            userid : userid,
            password : password,
            passwordCheck: passwordCheck,
            username : username,
            birthday : birthday,
            email : email
        }

        let json = null;
        try {
            json = await axios.post("/members",input_data)
        } catch (error) {
            console.log(error);
            window.alert(`[ ${e.response.status} ] ${e.response.statusText} \n ${e.message}`);
        }
        window.alert(json.data.item + "  ")
        // console.log(json);
        navigate("/");
    };

    return (
        <SignupContainer>
            <div className="signup_content">
                <h3 className="font2"></h3>

                <form onSubmit={onSubmit} ref={signup_form}>
                    {/*  */}
                    <div>
                        <label htmlFor="userid" className="message"> <span>*</span></label>
                        <input id="userid" name="userid" type="text" onBlur={onBlur} className="input_text" ></input>
                        <span id="err_id" className="err_msg" ></span>
                        <br />
                    </div>

                    {/*  */}
                    <div >
                    <label htmlFor="passord" className="message"> <span>*</span></label>
                    <input id="password" name="password" type="password" onBlur={onBlur} className="input_text" placeholder="+ 8~16"></input>
                    <span id="err_pw" className="err_msg"></span>
                    <br />
                    </div>

                    {/*   */}
                    <div>
                        <label htmlFor="passordCheck" className="message"> <span>*</span></label>
                        <input id="passwordCheck" name="passwordCheck" type="password" onBlur={onBlur} className="input_text"/>
                        <span id="err_pw_check" className="err_msg"></span>
                        <br />
                    </div>

                    {/*  */}
                    <div>
                        <label htmlFor="username" className="message"><span>*</span></label>
                        <input id="username" name="username" type="text" className="input_text" onBlur={onBlur}></input>
                        <span id="err_name" className="err_msg"></span>
                        <br />
                    </div>

                    {/*  */}
                    <div>
                        <label htmlFor="birth_year" className="message"></label>
                        <div className="birth_area">
                            <input id="birth_year" name="birth_year" type="number" placeholder="(4)" className="user_birth" maxLength={4} onBlur={onBlur} />
                            <select id="birth_month" name="birth_month" className="user_mm" placeholder="" onBlur={onBlur}>
                                <option value=""></option>
                                {month.map((v, i) => {
                                    return (
                                        <option value={v} key={i}>
                                            {v}
                                        </option>
                                    );
                                })}
                            </select>
                            <input id="birth_day" name="birth_day" type="number" placeholder="" className="user_birth" maxLength={2} onBlur={onBlur}/>
                        </div>
                        <span id="err_birth" className="err_msg"></span>
                        <br />
                    </div>

                    {/*  */}
                    <div className="email_area">
                        <label htmlFor="user_email" className="message"></label>
                        <div className="input_box">
                            <input id="input_email" name="input_email" type="text"  onBlur={onBlur} className="user_email" maxLength={20}/>
                            <div>@</div>
                            {/* <input type="text"></input> */}
                            <select id="input_domain" name="input_domain" type="text" onBlur={onBlur} className="user_email_sel">
                                {/*      */}
                                <option value="@gmail.com">gmail.com</option>
                                <option value="@naver.com">naver.com</option>
                                <option value="@daum.com">daum.com</option>
                            </select>
                        </div>
                    </div>
                    <span id="err_email" className="err_msg"></span>
                    <br />

                    <button type="submit" id="signup" className="btn_act">
                        
                    </button>
                </form>
            </div>
        </SignupContainer>
    );
};

export default Signup;
