import React, { useCallback, useRef } from "react";
import { NavLink, useNavigate } from "react-router-dom";
import styled from "styled-components";
import crypto from 'crypto-js';
import { useSelector, useDispatch } from "react-redux";
import { postLogin } from '../slices/MemberSlice';

import regexHelper from '../utils/RegexHelper';
import Spinner from '../components/Spinner';

const LoginContainer = styled.div`
    width: 100%;
    padding-top: 5%;
    padding-bottom: 20%;

    h3 {
        padding: 15% 0;
        display: flex;
        justify-content: center;
    }

    .login_content {
        width: 80%;
        display: flex;
        margin: 0 auto;
        flex-direction: column;
        flex-wrap: wrap;
        align-content: center;

        form {
            width: 100%;

            .input_text {
                width: 90%;
                height: 16px;
                padding: 5%;

                &:first-child {
                    margin-bottom: 3%;
                }
            }

            .login {
                margin-top: 7%;
            }

            .err_msg {
                color: red;
                font-size: 14px;
                padding-top: 4%;
                line-height: 1.5;
            }
        }
    }

    .find_btn_area {
        display: flex;
        flex-flow: row wrap;
        justify-content: space-between;

        .find_btn {
            margin: 7% 0;
            width: 48%;
        }
    }
`;

const Login = () => {
    //     
    const navigate = useNavigate();
    //     
    const dispatch = useDispatch();
    const { loading } = useSelector((state) => state.member);
    //   
    const errMsgRef = useRef();

    /** form submit  */
    const loginSubmit = useCallback(e => {
        e.preventDefault();

        //  
        const current = e.target;
        const userid = current.userid.value;
        let password = current.password.value;

        // ,   
        try { 
            regexHelper.value(userid, ' .');
            regexHelper.value(password, ' .');
        } catch(err) {
            //   
            errMsgRef.current.innerHTML = err.message;
            err.message === ' .' ? current.userid.focus() : current.password.focus();
            return;
        }

        /**  _crypto-js  */
        // AES  -->   
        const secretKey = process.env.REACT_APP_CRYPTO_KEY;
        password = crypto.AES.encrypt(password, secretKey).toString();
    
        //    
        dispatch(postLogin({
            userid: userid,
            password: password,
        }))
        .unwrap()
        .then(() => {
            //   
            navigate('/');
        })
        .catch(err => {
            //   
            errMsgRef.current.innerHTML = err.data.rtmsg;
        });
    }, [dispatch, navigate]);

    return (
        <>
            <Spinner visible={loading}/>
            
            <LoginContainer>
                <div className="login_content">
                    <h3 className="font2"></h3>
                    <form onSubmit={loginSubmit}>
                        <input type="text" name="userid" className="input_text" placeholder=""></input>
                        <input type="password" name="password" className="input_text" placeholder=""></input>
                        <div id="err_msg" className="err_msg" ref={errMsgRef}></div>
                        <button type="submit" name="login" className="login btn_act"></button>
                    </form>

                    {/* <div className="find_btn_area">
                        <button type="button" name="find_id" className="find_btn btn_dis"> </button>
                        <button type="button" name="find_pw" className="find_btn btn_dis"> </button>
                    </div> */}

                    <h3 className="font2">  ?</h3>

                    <NavLink to="/signup">
                        <button type="button" name="signup" className="btn_act"></button>
                    </NavLink>
                </div>
            </LoginContainer>
        </>
    );
};

export default Login;
