import React, { useEffect } from "react";
import styled from "styled-components";
import { useSelector, useDispatch } from "react-redux";
import { getPlaceList } from "../slices/PlaceSlice";

import Spinner from '../components/Spinner';
import ThemeItem from "../components/items/ThemeItem";
import TabArea from "../components/TabArea";

const HomeContainer = styled.div`
    width: 100%;
    height: 100%;
    padding-bottom: 10%;
    background-color: #e3eeff88;
`;

const Home = () => {
    // redux    
    const dispatch = useDispatch();
    const { data, loading, error } = useSelector((state) => state.place);

    //      
    useEffect(() => {
        dispatch(getPlaceList({
            rows: 5
        }));
    },[dispatch]);

    return (
        <HomeContainer>
            <TabArea />

            <Spinner visible={loading} />

            {data && (
                data.item.map((v, i) => <ThemeItem key={v.place_no} item={v}/>)
            )}
        </HomeContainer>
    );
};

export default Home;
