import React from 'react';

/**      custom hook */
const useMountedRef = () => {
    const mountedRef = React.useRef(false);

    React.useEffect(() => {
        setTimeout(() => {
            mountedRef.current = true;
        });
    }, []);
    
    return mountedRef;
};

export default useMountedRef;